import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


const Alert = {
    error(msg) {
        toast.error(msg);
    },
    success(msg) {
        toast.success(msg);
    },
    info(msg) {
        toast.info(msg);
    },

    promise(fct) {
        toast.promise(fct, {
            pending: "Post en cours de cration",
            success: "Post cr avec succs !",
            error: "Impossible de crer le post"
        })
    }


}

export default Alert